 body {
     background-color: #ffffff;
     font-family: 'Montserrat', sans-serif;
     font-weight: 400;
     font-size: 14px;
     color: #555;
 
     -webkit-font-smoothing: antialiased;
     -webkit-overflow-scrolling: touch;
 }
 
 /* Titles */
 h1, h2, h3, h4, h5, h6 {
   font-family: 'Montserrat', sans-serif;
     font-weight: 700;
     color: #333;
 }
 
 h1 {
   font-size: 35px;
   margin-top: 30px;
   margin-bottom: 30px;
 }
 
 
 
 /* Paragraph & Typographic */
 p {
     line-height: 28px;
     margin-bottom: 25px;
     font-size: 16px;
 }
 
 .centered {
     text-align: center;
 }
 
 /* Links */
 a {
     color: #1abc9c;
     word-wrap: break-word;
 
     -webkit-transition: color 0.1s ease-in, background 0.1s ease-in;
     transition: color 0.1s ease-in, background 0.1s ease-in;
 }
 
 a:hover,
 a:focus {
     color: #7b7b7b;
     text-decoration: none;
     outline: 0;
 }
 
 a:before,
 a:after {
     -webkit-transition: color 0.1s ease-in, background 0.1s ease-in;
     transition: color 0.1s ease-in, background 0.1s ease-in;
 }
 
  hr {
     display: block;
     height: 1px;
     border: 0;
     border-top: 1px solid #ccc;
     margin: 1em 0;
     padding: 0;
 }
 
 
 .navbar {
   text-transform: uppercase;
   margin-bottom: 0px;
 }
 
 .navbar-inverse {
   padding-bottom: 70px;
   padding-top: 70px;
 }
 
 .navbar-inverse {
   background-color: #1abc9c;
   border-color: #16a085;
 }
 
 .navbar-inverse .navbar-nav > li > a {
   color: white;
 }
 
 .navbar-inverse .navbar-nav > li > a:hover {
   color: #222222;
 }
 
 .navbar-brand {
   font-weight: 700;
   font-size: 20px;
   letter-spacing: 2px;
 }
 
 .navbar-inverse .navbar-brand {
   color: white;
 }
 
 .navbar-inverse .navbar-toggle {
   border-color: transparent;
 }
 
 .progress-bar-theme {
   background-color: #1abc9c;
 }
 
 
 /* Helpers */
 
 .mt {
   margin-top: 40px;
   margin-bottom: 40px;
 }
 
 .pt {
   padding-top: 50px;
   padding-bottom: 50px;
 }
 
 
 /* +++++ WRAP SECTIONS +++++ */
 
 #ww {
   padding-top: 70px;
   padding-bottom: 70px;
   background-color: #f2f2f2;
 }
 
 
 #footer {
   background-color: #2f2f2f;
   padding-top: 50px;
   padding-bottom: 50px; 
 }
 
 #footer p {
   color: white;
 }
 
 #footer h4 {
   color: white;
   text-transform: uppercase;
   padding-bottom: 20px;
 }
 
 
 /* +++++ Color Wraps / Blog Page +++++ */
 
 #grey {
   padding-top: 60px;
   padding-bottom: 60px;
   background-color: #f2f2f2;
 }
 
 #white {
   padding-top: 60px;
   padding-bottom: 60px;
   background-color: #ffffff;
 }
 
 
 
 /* Blog Date*/
 bd {
   font-size: 12px;
   text-transform: uppercase;
   color: #d2d2d2;
   font-weight: 700;
 }
 
 /* Blog Author*/
 ba {
   font-size: 12px;
   text-transform: uppercase;
 }
 
 /* Blog Quote */
 .bq {
   font-size: 22px;
   padding-top: 30px;
 }
 
 /* Blog Tags */
 bt {
   font-size: 12px;
 }
 
